body {
	 background: #fff;
	 font-family: "Delius", cursive;
}
 #app {
	 padding: 35px;
	 width: 40%;
	 margin: 20px auto;
	 border-radius: 10px;
}
 #app .playground {
	 display: flex;
	 flex-wrap: wrap;
	 justify-content: space-between;
	 perspective: 1000px;
	 margin-bottom: -15px;
}
 #app .playground .card {
	 width: 15%;
	 user-select: none;
	 height: 110px;
	 padding: 10px;
	 box-sizing: border-box;
	 text-align: center;
	 margin-bottom: 15px;
	 transition: 0.6s;
	 transform-style: preserve-3d;
	 position: relative;
}
 #app .playground .card div {
	 backface-visibility: hidden;
	 position: absolute;
	 top: 0;
	 left: 0;
	 width: 100%;
	 height: 100%;
	 border-radius: 10px;
	 transition: 0.6s;
	 background: #f4f5f7;
}
 #app .playground .card .front {
	 /*font-size: 50px;
	 line-height: 120px;
	 cursor: pointer;
	 color: #b9bfcc;
	 display: flex;
	 align-items: center;
	 justify-content: center;*/
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: linear-gradient(45deg, #1fa5ff 25%, #1053ff 25%, #1053ff 50%, #1fa5ff 50%, #1fa5ff 75%, #1053ff 75%, #1053ff 100%);
	background-size: 56px 56px;/*
	&::before, &::after {
		@include border-radius($card-border-radius - 2);
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: linear-gradient(#20E4A4, #2CC9CC);
		opacity: .8;
	}
	&::after {
		opacity: 0;
		transition: opacity $animation-duration * 2;
	}*/
}
 #app .playground .card .back {
	 transform: rotateY(180deg);
	 line-height: 110px;
}
 #app .playground .card .back img {
	 vertical-align: middle;
	 width: 70%;
}
 #app .playground .card.opened {
	 transform: rotateY(180deg);
}
 #app .playground .card.matched {
	 transform: rotateY(180deg);
}
 #app .playground .card.matched .back {
	 box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.05) inset;
	 animation: selected 0.8s 0s ease 1;
	 animation-fill-mode: both;
	 opacity: 0.2;
}
