 @keyframes selected {
	 0% {
		 opacity: 0.2;
	}
	 30% {
		 opacity: 0.5;
	}
	 50% {
		 opacity: 0.9;
	}
	 70% {
		 opacity: 0.2;
	}
	 100% {
		 opacity: 0.3;
	}
}
 